import { Avatar } from '@material-ui/core';
import React from 'react';
import '../assets/css/Post.css';
export default function Post({caption,userName,imageUrl}){
    return (
        <div className="post__main">
            {/* Header == avatar  and UserName */}
            <div className="pm__avatar__userName">
            <span className="pm__avatar">
                <Avatar className="avatar">H</Avatar>
            </span>
            <span className="pm__userName">
                <h4>{userName}</h4>
            </span>
            </div>
            {/* Image */}
                <img className="pm__image" alt="INsta Image" src={imageUrl}/>
            {/* UserName and caption */}
            <div className="pm__caption_userName">
                <span className="pm__userName"><strong>{userName}</strong></span>
                <span className="pm__caption">{caption}</span>
            </div>

        </div>
    );
}